.tgl {
  display: none; // add default box-sizing for this scope
  &,
  &:after,
  &:before,
  & *,
  & *:after,
  & *:before,
  &+.tgl-btn {
    box-sizing: border-box;
    &::selection {
      background: none;
    }
  }
  +.tgl-btn {
    outline: 0;
    display: block;
    width: 7rem;
    height: 1.8rem;
    position: relative;
    cursor: pointer;
    user-select: none;
    &:after,
    &:before {
      position: relative;
      display: block;
      content: "";
      width: 50%;
      height: 100%;
    }
    &:after {
      left: 0;
    }
    &:before {
      display: none;
    }
  }
  &:checked+.tgl-btn:after {
    left: 50%;
  }
}

.tgl-skewed {
  +.tgl-btn {
    overflow: hidden;
    transform: skew(-10deg);
    backface-visibility: hidden;
    transition: all .2s ease;
    font-family: sans-serif;
    background: $green;
    &:after,
    &:before {
      transform: skew(10deg);
      display: inline-block;
      transition: all .2s ease;
      width: 100%;
      text-align: center;
      position: absolute;
      line-height: 2em;
      font-weight: bold;
      color: #fff;
      text-shadow: 0 1px 0 rgba(0, 0, 0, .4);
    }
    &:after {
      left: 100%;
      content: attr(data-tg-on);
    }
    &:before {
      left: 0;
      content: attr(data-tg-off);
    }
    &:active {
      background: $green;
      &:before {
        left: -10%;
      }
    }
  }
  &:checked+.tgl-btn {
    background: #888;
    &:before {
      left: -100%;
    }
    &:after {
      left: 0;
    }
    &:active:after {
      left: 10%;
    }
  }
}

.flex-no-shrink {
  flex-shrink: 0;
}

//page loader
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
