//status circle
.status-disabled {
  color: $red;
  &:link,
  &:visited,
  &:hover {
    color: $red;
    text-decoration: none;
  }
  .fa-minus-circle::after {
    display: inline-block;
    font-family: $font-family-base;
    font-size: $font-size-sm;
    content: "Disabled";
    margin-left: 4px
  }
}

.status-enabled {
  color: $green;
  &:link,
  &:visited,
  &:hover {
    color: $green;
    text-decoration: none;
  }
  .fa-check-circle::after {
    display: inline-block;
    font-family: $font-family-base;
    font-size: $font-size-sm;
    content: "Active";
    margin-left: 4px
  }
}

.status-enabled .fa-plus-square,
.status-disabled .fa-minus-square {
  margin-right: 10px;
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 2.5rem, 0);
    transform: translate3d(0, 2.5rem, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

